<template>
  <div>
    <raw-loader :mytxt="require('!raw-loader!./VueTabulatorInlineEditor.vue').default"></raw-loader>
    {{ msg }}<br>
    <!-- {{myvalid}}<br> -->
    <!-- rowedit : {{rowedit}}<br>   -->
    sel : {{ sel }}<br>
    valid : {{ valid }}<br>
    {{ tableData }}
    <div class="mb-1">
      <button class="mr-1" @click="onAdd">Add</button>
      <!-- <button class="mr-1" @click="testData" >Test data</button> -->
      <!-- <button class="mr-1" @click="testEdit" >Test Edit</button> -->
    </div>
    <!-- {{tableData}} -->
    <div ref="table"></div>
    <b-modal ref="my-modal2" id="modal-1" size="sm" :no-close-on-backdrop="true" hide-footer title="BootstrapVue">
      <form @submit.stop.prevent="onSaveModal">
        <div>
          <!-- <pre>{{r}}</pre> -->
          <div class="form-group row">
            <label class="col-sm-4 col-form-label">Id</label>
            <div class="col-sm-8">
              <input type="text" class="form-control" disabled v-model="r.id" />
            </div>
          </div>
          <div class="form-group row">
            <label class="col-sm-4 col-form-label">
              Name
            </label>
            <div class="col-sm-8">
              <input type="text" v-model="r.name" name="name" v-validate="'required'" :class="{
                'form-control': true,
                'ko-hop-le': errors.has('name'),
              }" autocomplete="off" />
            </div>
          </div>

          <div class="form-group row">
            <label class="col-sm-4 col-form-label">
              Ngay
            </label>
            <div class="col-sm-8">
              <b-form-datepicker v-model="r.ngay" name="ngay" v-validate="'required'"
                :date-format-options="{ year: 'numeric', month: 'numeric', day: 'numeric' }" locale="vi" class="mb-2">
              </b-form-datepicker>
            </div>
          </div>
          <div class="form-group row">
            <label class="col-sm-4 col-form-label">
              Act
            </label>
            <div class="col-sm-8">
              <b-form-checkbox v-model="r.act" name="checkbox-1" value=true unchecked-value=false @change="ChangeAct">
                Active
              </b-form-checkbox>
            </div>
          </div>

          <div class="form-group row">
            <label class="col-sm-4 col-form-label">
              Thành phố {{ errors.has('tp') }}
            </label>
            <div class="col-sm-8">
              <v-select v-model="r.tp" name="tp" v-validate="'required'" :reduce="(option) => option.id" :options="tps"
                :has-error="errors.has('tp')" :class="{ danger: errors.has('tp') }" />
            </div>
          </div>
          <div class="form-group row">
            <label class="col-sm-4 col-form-label">
              Quyền
            </label>
            <div class="col-sm-8">
              <v-select multiple name="quyen" v-model="r.quyen" :options="['Admin', 'User', 'Editor']"
                v-validate="'required'" :class="{ danger: errors.has('quyen') }" />
            </div>
          </div>

        </div>
        <div class="float-right">
          <button type="submit" class="btn btn-primary mr-1 btn-sm">Save</button>
          <button type="button" class="btn btn-sm" @click="hideModal()">Cancel</button>
        </div>
      </form>
    </b-modal>
  </div>
</template>
<script>
import { TabulatorFull as Tabulator } from 'tabulator-tables'; //import Tabulator library
//import $ from "jquery"
import MyLoader from "@/func/MyLoad.js";


import VeeValidate from "vee-validate";
import Vue from "vue";
Vue.use(VeeValidate, {
  classes: true,
});

import vSelect from "vue-select";
Vue.component("v-select", vSelect);
import "vue-select/dist/vue-select.css";
import moment from 'moment';
export default {
  data: function () {
    return {
      tabulator: null, //variable to hold your table
      tableData: [], //data for table to display
      msg: "hello world",
      count: 8,
      r: {
        id: 0,
        ten: 'thuc 101',
        ngay: new Date
      },
      tps: [
        { label: 'Huế', id: 'HUE' },
        { label: 'Sài gòn', id: 'SG' },
        { label: 'Đà nẵng', id: 'DN' },
        { label: 'Quảng ngãi', id: 'QN' },
      ],
      sel: {},
      valid: '',
      oldColor: '',

      mons: [
        { id: 1, name: 'Gạo' },
        { id: 2, name: 'Đường' },
        { id: 3, name: 'Muối' },
        { id: 4, name: 'Bột ngọt' },
        { id: 5, name: 'Nước ngọt' },
        { id: 6, name: 'Cà phê' },
        { id: 7, name: 'Bia' },
        { id: 8, name: 'Nước mía' }
      ]


    }
  },
  mounted() {
    if (!document.getElementById("myScript")) {
      MyLoader.loadScript("myScript", "/js/jquery.min.js").then(() => {
        MyLoader.loadScript("", "/js/bootstrap.js").then(() => {
          MyLoader.loadScript("", "/js/select2.min.js").then(() => {
            MyLoader.loadScript("", "/js/bootstrap-datepicker.js").then(() => {
              MyLoader.loadScript("", "/js/moment.js").then(() => {
                MyLoader.loadCss("/css/select2.min.css").then(() => {
                  MyLoader.loadCss("/css/bootstrap-datepicker.css").then(() => {
                    this.onReady();
                  })
                })
              })
            })
          })
        })
      })
    } else {
      this.onReady();
    }
  },
  methods: {
    onReady() {
      this.loadTabulator();
    },
    loadTabulator() {
      var that = this;
      var inputEditor = function (cell, onRendered, success, cancel, editorParams) {
        var editor = document.createElement("input");
        $(editor).css({
          // "padding": "4px",
          // "width": "100%",
          // "box-sizing": "border-box",
          // "height": "100%",
          // "border": "1px solid blue",
          // "background-color": "yellow",
        })
        $(editor).val(cell.getValue());
        onRendered(function () {
          $(editor).focus();
          $(editor).css("width", "80%");
        });
        $(editor).on("change", function () {
          success($(editor).val());
        })
        $(editor).on("blur", function () {
          success($(editor).val());
        })
        return editor;
      };
      var comboGEditor = function (cell, onRendered, success, cancel, editorParams) {
        var editor = document.createElement("select");
        for (var i = 0; i < editorParams.length; i++) {
          var opt = document.createElement('option');
          opt.value = editorParams[i].key;
          opt.innerHTML = editorParams[i].name;
          editor.appendChild(opt);
        }
        $(editor).css({
          "padding": "4px",
          "width": "100%",
          "box-sizing": "border-box",
        })
        editor.value = cell.getValue();
        onRendered(function () {
          editor.focus();
          editor.style.css = "100%";
        });
        function successFunc() {
          success(editor.value);
        }
        editor.addEventListener("change", successFunc);
        editor.addEventListener("blur", successFunc);
        return editor;
      };

      var testFormatter = function (cell, formatterParams, onRendered) {
        onRendered(function () {
          let $cell = $(cell.getElement());
          $cell.find("button").on("click", function () {
            alert(editable);
          })
        });
        let $el = $("<div>");
        $el.css({
          //"padding": "4px",
          //"width": "100%",
          //"box-sizing": "border-box",
          //"height": "100%",
          //"border":"1px solid blue",
          //"background-color":"yellow",
        })
        let editable = (that.sel.id == cell.getRow().getData().id);
        if (editable) {
          $el.append($("<button>").text("hello"));
        } else {
          $el.append($("<button>").text("hello").attr('disabled', 'disabled'));
        }
        return $el[0];
      };
      //formatter:testFormatter  

      var select2Formatter = function (cell, formatterParams, onRendered) {
        onRendered(function () {
          let $myCell = $(cell.getElement());
          let $cell = $(cell.getElement()).find("select");
          $cell.css({
            "padding": "4px",
            "width": "100%",
            "box-sizing": "border-box",
            "font-family": "",
          })
          $cell.select2({
            placeholder: '',
            width: "100%",
            allowClear: true,
          });
          $cell.focus();
          $cell.val(cell.getValue()).trigger("change.select2");
          $(cell.getElement())
            .find("select")
            .on("change", function () {
              cell.setValue($(this).val());
            });
          $myCell
            .find(".select2-container--default")
            .css("border", "0px solid blue");
          $myCell
            .find(".select2-selection--single")
            .css("border", "0px solid blue");
        });
        var $el = $("<select class='tabu' >");
        let editable = (that.sel.id == cell.getRow().getData().id);
        if (editable == false) {
          $el.attr('disabled', 'disabled');
        } else {
        }
        formatterParams.forEach((el) => {
          let $option = $("<option>").attr("value", el.id).text(el.text);
          $el.append($option);
        });
        return $el[0];
      };

      var comboFormatter = function (cell, formatterParams, onRendered) {
        onRendered(function () {
          let $cell = $(cell.getElement());
          $cell.find("select").on("change", function () {
            cell.setValue($(this).val());
          })
        });
        let $el = $("<select class='form-control'>");
        $el.append($("<option>").attr("value", "").text(""))
        formatterParams.forEach(x => {
          $el.append($("<option>").attr("value", `${x.id}`).text(`${x.text}`))
        })
        $el.val(cell.getValue());
        $el.css({
          "padding": "3px",
          "width": "100%",
          //"box-sizing": "border-box",
          "height": "100%",
          //"border":"1px solid blue",
          //"background-color":"yellow",
        })
        let editable = (that.sel.id == cell.getRow().getData().id);
        if (editable == false) {
          $el.attr('disabled', 'disabled');
        } else {
        }
        return $el[0];
      };
      //formatter:testFormatter

      var datePickerFormatter = function (cell, formatterParams, onRendered) {
        onRendered(function () {
          let $cell = $(cell.getElement()).find("input");
          let editable = (that.sel.id == cell.getRow().getData().id);
          if (editable == false) {
            $(cell.getElement()).find("input:first").prop("disabled", true);
          } else {
          }
          $cell.css({
            "padding": "4px",
            "width": "100%",
            "box-sizing": "border-box",
            "height": "100%",
            //"border":"1px solid blue",
            //"background-color":"yellow",
          })
          $cell.prop("readonly", true);
          $cell.datepicker({
            format: "dd/mm/yyyy",
            todayBtn: true,
            autoclose: true,
            todayHighlight: true,
          });
          let value = moment(cell.getValue(), "DD/MM/YYYY").format("DD/MM/YYYY");
          $cell.datepicker("setDate", value);
          $cell.datepicker().on("changeDate", function () {
            let d = $(this).datepicker("getDate");
            cell.setValue(moment(d).format("DD/MM/YYYY"));
          });
        });
        var $el = $("<input class='form-control' />");
        return $el[0];
      };

      // create custom formatter
      var selectFormatter = function (cell, formatterParams) {
        //create and style select
        var select = $("<select></select>");
        //add options elements
        formatterParams.forEach(function (item) {
          select.append($("<option></option>").attr("value", item).text(item));
        });
        //define styling
        select.css({
          "padding": "4px",
          "width": "100%",
          "box-sizing": "border-box",
          "font-family": "",
        })
          .val(cell.getValue())
        //submit new value on blur
        select.on("change blur", function (e) {
          cell.setValue(select.val());
        });
        //submit new value on enter
        select.on("keydown", function (e) {
          if (e.keyCode === 13) {
            cell.setValue(select.val());
          }
        });

        return select;
      }
      //in column definition assign formatter and define options 
      //{title:"Gender", field:"gender", formatter:selectFormatter, formatterParams:["male", "female"]}


      var combo2Editor = function (cell, onRendered, success, cancel, editorParams) {
        var editor = document.createElement("input");
        $(editor).css({
          "padding": "4px",
          "width": "100%",
          "box-sizing": "border-box",
          "height": "100%",
          "border": "1px solid blue",
          "background-color": "yellow",
        })
        $(editor).val(cell.getValue());
        onRendered(function () {
          $(editor).focus();
        });
        $(editor).on("change", function () {
          success($(editor).val());
        })
        $(editor).on("blur", function () {
          success($(editor).val());
        })
        return editor;
      };
      var comboEditor = function (cell, onRendered, success, cancel, editorParams) {
        var $editor = $("<select></select>");
        $editor.append("<option>one</option>");
        $editor.append("<option>two</option>");
        $editor.append("<option>three</option>");
        $editor.css({
          "padding": "4px",
          "width": "100%",
          "box-sizing": "border-box",
          "height": "100%",
          "border": "1px solid blue",
          "background-color": "yellow",
        })
        $editor.val(cell.getValue());
        $editor.on("change", function () {
          success($(this).val());
        });
        return $editor[0];
      };
      var combo2Editor = function (cell, onRendered, success, cancel, editorParams) {
        var $editor = $("<select></select>");
        let ar = [
          { id: "one", text: "One" },
          { id: "two", text: "Two" },
          { id: "three", text: "Three" }
        ];
        ar.forEach(x => {
          $editor.append($("option").val(x.id).text(`${x.id} - ${x.text}`));
        })
        $editor.css({
          "padding": "4px",
          "width": "100%",
          "box-sizing": "border-box",
          "height": "100%",
          "border": "1px solid blue",
          "background-color": "yellow",
        })
        $editor.val(cell.getValue());
        $editor.on("change blur", function () {
          success($editor.val());
        });
        return $editor[0];
      };



      var roweditCheck = function (cell) {
        let row = cell.getRow();
        let id = row.getIndex();
        let $cell = row.getElement();
        if (that.sel.id == cell.getRow().getData().id) {
          return true;
        } else {
          return false;
        }
      }

      this.tabulator = new Tabulator(this.$refs.table, {
        //selectable:true,
        //columnVertAlign:"middle",//"bottom",//middle
        data: this.tableData, //link data to table
        reactiveData: true, //enable data reactivity
        height: $(window).innerHeight() - 400,//'200px',//$(window).innerHeight()
        //addRowPos:"top",       //top,bottom//when adding a new row, add it to the top of the table
        // columnVertAlign: "middle", //align header contents
        history: true,             //allow undo and redo actions on the table
        pagination: "local",       //paginate the data
        paginationSize: 3,         //allow 7 rows per page of data
        paginationCounter: "rows", //display count of paginated rows in footer
        paginationInitialPage: 1, //optional parameter to set the initial page to load    
        paginationSizeSelector: [5, 10, 20, 30, 50],
        rowFormatter: function (row) {
          let chk = that.sel.id == row.getData().id;
          if (chk == true) {
            $(row.getElement()).css("background-color", "yellow");
          } else {
            //$(row.getElement().child[0])
            //.css("background-color", rgb(255, 255, 255));
            //.css("background-color", "blue");
            //row.getElement().childNodes[0].style.backgroundColor="silver";            
          }

        },
        //autoColumns:true,
        columns: [
          {
            title: "STT",
            formatter: "rownum",
            headerHozAlign: "center",
            hozAlign: "center",
            vertAlign: "middle",//"bottom",
          },
          {
            title: "Cmd",
            headerHozAlign: "center",
            hozAlign: "center",
            vertAlign: "middle",//"bottom",
            headerSort: false,
            width: 100,
            formatter: function (cell, formatterParams, onRendered) {
              onRendered(function () {
                $(cell.getElement()).find("#testedit").on("click", cell, that.onTestEdit);
                $(cell.getElement()).find("#save").on("click", cell.getRow(), that.onSave);
                $(cell.getElement()).find("#cancel").on("click", cell.getRow(), that.onCancel);
                $(cell.getElement()).find("#edit").on("click", cell.getRow(), that.onEdit);
                $(cell.getElement()).find("#del").on("click", cell.getRow(), that.onDel);
              });
              let $div = $("<div>");
              let $btnSave = $("<button>", { text: "Save", class: "mr-1" }).attr("id", "save").hide();
              let $btnCancel = $("<button>", { text: "Cancel", class: "mr-1" }).attr("id", "cancel").hide();
              let $btnEdit = $("<button>", { text: "Edit", class: "mr-1" }).attr("id", "edit");
              let $btnDel = $("<button>", { text: "Del", class: "mr-1" }).attr("id", "del");
              $div.append($btnSave);
              $div.append($btnCancel);
              $div.append($btnEdit);
              $div.append($btnDel);
              if (that.sel.id == cell.getRow().getData().id) {
                $btnSave.show();
                $btnCancel.show();
                $btnEdit.hide();
                $btnDel.hide();
              } else {
                if ($.isEmptyObject(that.sel) == true) {
                  $btnSave.hide();
                  $btnCancel.hide();
                  $btnEdit.show();
                } else {
                  $btnEdit.attr('disabled', 'disabled');//.hide();
                  $btnDel.attr('disabled', 'disabled');//.hide();
                }
              }
              return $div[0];
            },
          },
          {
            title: "Id",
            field: "id",
            width: 50,
            vertAlign: "middle",//"bottom",
            //headerFilter: true,
            hozAlign: "center",
          },
          {
            title: "Họ",
            field: "ho",
            width: 80,
            vertAlign: "middle",//"bottom",
            editor: inputEditor,
            validator: ["required"], //"required"  
            editable: roweditCheck,//editCheck,
          },
          {
            title: "Name",
            field: "name",
            width: 80,
            vertAlign: "middle",//"bottom",
            editor: "input",
            validator: ["required"], //"required"  
            editable: roweditCheck,//editCheck,
          },
          {
            title: "Tp",
            field: 'tp',
            width: 100,
            vertAlign: "middle",//"bottom",
            headerHozAlign: 'center',
            hozAlign: 'center',
            editor: comboGEditor,//comboEditor,
            editorParams: [
              { "key": "hue", "name": "Huếxxxxxx" },
              { "key": "sg", "name": "Sài gòn" },
              { "key": "hn", "name": "Hà nội" }
            ],
            formatter: function (cell, formatterParams, editorParams) {
              for (var i = 0; i < formatterParams.length; i++) {
                if (formatterParams[i].key == cell.getValue()) {
                  return formatterParams[i].name;
                }
              }
            },
            formatterParams: [
              { "key": "hue", "name": "Huế xyz" },
              { "key": "sg", "name": "Sài gòn" },
              { "key": "hn", "name": "Hà nội" }
            ],
            editable: roweditCheck,//editCheck,
          },
          {
            title: "Test",
            width: 80,
            hozAlign: "center",
            vertAlign: "middle",//"bottom",
            formatter: testFormatter,
            editable: roweditCheck,
          },
          {
            title: "MonId",
            field: "monid",
            headerHozAlign: "center",
            hozAlign: "left",
            width: 140,
            validator: ["required"], //"required"  
            formatter: select2Formatter,
            formatterParams: [
              { id: 1, name: 'Gạo' },
              { id: 2, name: 'Đường' },
              { id: 3, name: 'Muối' },
              { id: 4, name: 'Bột ngọt' },
              { id: 5, name: 'Nước ngọt' },
              { id: 6, name: 'Cà phê' },
              { id: 7, name: 'Bia' },
              { id: 8, name: 'Nước mía' }
            ].map((x) => ({
              id: x.id,
              text: `${x.id} - ${x.name}`,
            })),
            editable: roweditCheck,//editCheck,
          },
          {
            title: "Trình độ",
            field: "td",
            width: 100,
            hozAlign: "center",
            formatter: comboFormatter,
            formatterParams: [
              { id: "dh", text: "Đại học" },
              { id: "tc", text: "Trung cấp" },
              { id: "cd", text: "Cao đẳng" }
            ],
            editable: roweditCheck,
          },
          {
            title: "Ngày",
            field: "ngay",
            width: 100,
            hozAlign: "center",
            formatter: datePickerFormatter,
            editable: roweditCheck,
          },

        ], //define table columns
      });
      this.tabulator.on("dataLoaded", function (data) {
        that.onLoad();
      });
      this.tabulator.on("pageLoaded", function (pageno) {
      });
      this.tabulator.on("dataChanged", function (data) {
      });
      this.tabulator.on("rowClick", function (e, row) {
      });
      this.tabulator.on("rowSelectionChanged", function (data, rows) {
      });
      this.tabulator.on("rowSelected", function (row) {
        let index = row.getIndex();
        that.msg = index;
        console.log('rowSelected');
      });
      this.tabulator.on("cellEditing", function (cell) {
        let row = cell.getRow();
        let data = row.getData();
        let index = row.getIndex();
      });
      this.tabulator.on("cellEditCancelled", function (cell) {
      });
      this.tabulator.on("cellEdited", function (cell) {
        //console.log(`cell edited`);
        alert(cell.getField());
        // let row = cell.getRow();   
        // let data=row.getData();
        // let index=row.getIndex();
        // //alert(JSON.stringify(data));
        // let solg=data.solg;
        // let dongia=data.dongia;
        // let tien=solg*dongia;
        //console.log(tien);
      });
      this.tabulator.on("validationFailed", function (cell, value, validators) {
        let field = cell.getField();
        that.valid = field;
      });
    },
    onLoad() {

      let ar = [
        { id: 1, ho: 'Nguyễn Tấn', name: "thuc 101", tp: "hue", gt: "male", monid: 1, td: "dh", ngay: new Date() },
        { id: 2, ho: 'Nguyễn Tấn', name: "thuc 102", tp: "hue", gt: "male", monid: 1, td: "dh", ngay: new Date() },
        { id: 3, ho: 'Nguyễn Tấn', name: "thuc 102", tp: "hue", gt: "male", monid: 1, td: "dh", ngay: new Date() }
      ];
      this.count = 4;
      ar.forEach(x => {
        this.tableData.push(x);
      })
    },
    ChangeAct() {
      let act = (this.r.act == 'true') ? true : false;
      this.r.act = act;
    },
    onSave(e) {
      if (this.valid != "") {
        this.toast("không hợp lệ!!!")
        return;
      }
      e.preventDefault();
      let row = e.data;
      var cells = row.getCells();
      let id = row.getIndex();
      let index = this.tableData.findIndex(x => x.id == id);
      let r = JSON.parse(JSON.stringify(this.tableData[index]));// this.tableData[index]
      this.sel = {};
      this.tabulator.redraw(true);
    },
    onSaveModal() {
      this.$validator.validateAll().then((result) => {
        if (!result) {
          this.toast("Không hợp lệ");
          return;
        }
        let r = JSON.parse(JSON.stringify(this.r));
        if (r.id == 0) {
          r.id = this.count;
          this.tabulatorAdd(r);
          this.count++;
          this.setPage(1);
        } else {
          this.tabulatorUpdate(r);
        }
        this.hideModal();
      });
    },
    onAdd() {
      let r = {
        id: this.count,
        name: "thuc 101",
      }
      this.tabulatorAdd(r);
      this.count++;
      this.setPage(1);
    },
    onCancel(e) {
      e.preventDefault();
      let row = e.data;
      var cells = row.getCells();
      let id = row.getIndex();
      let index = this.tableData.findIndex(x => x.id == id);
      let r = JSON.parse(JSON.stringify(this.sel));
      this.tabulatorUpdate(r);
      this.sel = {};
      this.valid = "";
      let $row = $(row.getElement());
      this.tabulator.redraw(true);
    },
    onEdit(e) {
      e.preventDefault();
      let row = e.data;
      var cells = row.getCells();
      let id = row.getIndex();
      let index = this.tableData.findIndex(x => x.id == id);
      this.sel = JSON.parse(JSON.stringify(row.getData()));
      let $row = $(row.getElement());
      this.tabulator.redraw(true)
    },
    onDel(e) {
      e.preventDefault();
      let row = e.data;
      let id = row.getIndex();
      this.tabulatorDel(id);
    },
    getPageMax() {
      return this.tabulator.getPageMax();
    },
    getLastPage(count) {
      let pageSize = this.tabulator.getPageSize();
      let du = count % pageSize;
      return du == 0 ? count / pageSize : (count - du) / pageSize + 1;
    },
    getPageSize() {
      return this.tabulator.getPageSize();
    },
    getPage() {
      return this.tabulator.getPage();
    },
    setPage(_page) {
      this.tabulator.setPage(_page);
    },
    getData() {
      return this.tabulator.getData();
    },
    tabulatorAdd(r) {
      this.tableData.unshift(r);
    },
    tabulatorDel(id) {
      let index = this.tableData.findIndex(x => x.id == id);
      this.tableData.splice(index, 1);
    },
    tabulatorUpdate(r) {
      let index = this.tableData.findIndex(x => x.id == r.id);
      this.tableData.splice(index, 1, r);
    },
    tabulatorGetRow(id) {
      //let index=this.tabulator.getData().findIndex(x=>x.id==id);
      //let r=this.tabulator.getData()[index];
      //return r;
    },
    toast(msg, variant = null) {
      this.$bvToast.toast(msg, {
        title: `Information`,
        variant: variant,
        solid: true,
      });
    },
    showModal() {
      this.$refs['my-modal2'].show()
    },
    hideModal() {
      this.$refs['my-modal2'].hide()
    },
  }
}
</script>
