 this.setPage(1);\r\n        } else {\r\n          this.tabulatorUpdate(r);\r\n        }\r\n        this.hideModal();\r\n      });\r\n    },\r\n    onAdd() {\r\n      let r = {\r\n        id: this.count,\r\n        name: \"thuc 101\",\r\n      }\r\n      this.tabulatorAdd(r);\r\n      this.count++;\r\n      this.setPage(1);\r\n    },\r\n    onCancel(e) {\r\n      e.preventDefault();\r\n      let row = e.data;\r\n      var cells = row.getCells();\r\n      let id = row.getIndex();\r\n      let index = this.tableData.findIndex(x => x.id == id);\r\n      let r = JSON.parse(JSON.stringify(this.sel));\r\n      this.tabulatorUpdate(r);\r\n      this.sel = {};\r\n      this.valid = \"\";\r\n      let $row = $(row.getElement());\r\n      this.tabulator.redraw(true);\r\n    },\r\n    onEdit(e) {\r\n      e.preventDefault();\r\n      let row = e.data;\r\n      var cells = row.getCells();\r\n      let id = row.getIndex();\r\n      let index = this.tableData.findIndex(x => x.id == id);\r\n      this.sel = JSON.parse(JSON.stringify(row.getData()));\r\n      let $row = $(row.getElement());\r\n      this.tabulator.redraw(true)\r\n    },\r\n    onDel(e) {\r\n      e.preventDefault();\r\n      let row = e.data;\r\n      let id = row.getIndex();\r\n      this.tabulatorDel(id);\r\n    },\r\n    getPageMax() {\r\n      return this.tabulator.getPageMax();\r\n    },\r\n    getLastPage(count) {\r\n      let pageSize = this.tabulator.getPageSize();\r\n      let du = count % pageSize;\r\n      return du == 0 ? count / pageSize : (count - du) / pageSize + 1;\r\n    },\r\n    getPageSize() {\r\n      return this.tabulator.getPageSize();\r\n    },\r\n    getPage() {\r\n      return this.tabulator.getPage();\r\n    },\r\n    setPage(_page) {\r\n      this.tabulator.setPage(_page);\r\n    },\r\n    getData() {\r\n      return this.tabulator.getData();\r\n    },\r\n    tabulatorAdd(r) {\r\n      this.tableData.unshift(r);\r\n    },\r\n    tabulatorDel(id) {\r\n      let index = this.tableData.findIndex(x => x.id == id);\r\n      this.tableData.splice(index, 1);\r\n    },\r\n    tabulatorUpdate(r) {\r\n      let index = this.tableData.findIndex(x => x.id == r.id);\r\n      this.tableData.splice(index, 1, r);\r\n    },\r\n    tabulatorGetRow(id) {\r\n      //let index=this.tabulator.getData().findIndex(x=>x.id==id);\r\n      //let r=this.tabulator.getData()[index];\r\n      //return r;\r\n    },\r\n    toast(msg, variant = null) {\r\n      this.$bvToast.toast(msg, {\r\n        title: `Information`,\r\n        variant: variant,\r\n        solid: true,\r\n      });\r\n    },\r\n    showModal() {\r\n      this.$refs['my-modal2'].show()\r\n    },\r\n    hideModal() {\r\n      this.$refs['my-modal2'].hide()\r\n    },\r\n  }\r\n}\r\n</script>\r\n<style lang=\"scss\">\r\n.select2-container--default .select2-selection--single .select2-selection__arrow {\r\n  height: 15px;\r\n  position: absolute;\r\n  top: 4px;\r\n  right: 1px;\r\n  width: 20px;\r\n}\r\n\r\n.select2-container--default .select2-selection--single .select2-selection__rendered {\r\n  color: #444;\r\n  line-height: 24px;\r\n}\r\n\r\n.select2-container .select2-selection--single {\r\n  box-sizing: border-box;\r\n  cursor: pointer;\r\n  display: block;\r\n  height: 24px;\r\n  -moz-user-select: none;\r\n  -ms-user-select: none;\r\n  user-select: none;\r\n  -webkit-user-select: none;\r\n}\r\n\r\n.xyz {\r\n  border: 1px solid red;\r\n}\r\n\r\n.danger,\r\n.danger .dropdown-toggle,\r\n.danger .selected-tag {\r\n  background-color: aqua;\r\n  border-radius: 4px;\r\n}\r\n\r\n.ko-hop-le {\r\n  border-radius: 4px;\r\n  background-color: aqua;\r\n}\r\n\r\n$textSize: 8.5px; //14px !default; //table text size\r\n@import \"~tabulator-tables/src/scss/tabulator.scss\";\r\n\r\n.tabulator-col-title {\r\n  text-align: center;\r\n}\r\n\r\n.tabulator .tabulator-footer .tabulator-page.active {\r\n  background-color: greenyellow !important;\r\n  color: blue !important;\r\n}\r\n\r\n.tabulator-row .tabulator-cell {\r\n  padding: 2px 4px;\r\n}\r\n\r\n.b-calendar .b-calendar-grid-body .col[data-date] .btn {\r\n  width: 24px;\r\n  height: 24px;\r\n  font-size: 10px;\r\n  line-height: 1;\r\n  margin: 0px auto;\r\n  padding: 6px 0;\r\n}\r\n\r\n.custom-control.custom-checkbox {\r\n  position: relative;\r\n  z-index: 1;\r\n  display: block;\r\n  min-height: 0.9rem;\r\n  padding-left: 1.5rem;\r\n  padding-top: 0.3rem;\r\n}\r\n\r\n.col-4 input {\r\n  margin-bottom: 1px;\r\n}\r\n\r\n.form-group {\r\n  margin-bottom: 0.3rem;\r\n}\r\n</style>\r\n\r\n";